import React, { useState, Suspense, useContext, useEffect } from "react";
import * as THREE from "three";
import { useContextBridge, Loader } from "@react-three/drei";
import Scene from "../Scene";
import { Canvas, useFrame, useThree } from "@react-three/fiber";
import BoxContent from "../HtmlBS/BoxContent";
import { Button, IconButton } from "@material-ui/core";
import * as Models from "./../Helpers/GLBReroute";
import { Debug, Physics } from "@react-three/cannon";
import img from "../../images/lightbulb.png"
import { DataContext } from "../../App";
import ReactGA from 'react-ga';

/*

So what you do is:
Import context bridge 
create a scene wrapper that holds a canvas and camera,s and wraps the scene with the bridge
and then within the scene we go ahead and consume both contexes.
make sure to wrap the scene wrapper with the first context.
*/

const DefaultText = {
  text: {
    enable: false,
    content: () => () => console.log("nothing"),
    title: "",
    beingDragged: false,
  },
  setText: (state) => {},
};
const DefaultButton = {
  color: "#0400ff",
  setColor: (state) => {},
};

export const TextContext = React.createContext(DefaultText);
export const ButtonContext = React.createContext(DefaultButton);
function ContextBridger() {
  const ContextBridge = useContextBridge(TextContext, ButtonContext);

  return (
    <>
      <div className="absolute h-screen w-screen">
        <Canvas shadows frameloop="demand">
          {/* create the bridge inside the Canvas and forward the context */}
          <ContextBridge>
            <Scene />
          </ContextBridge>
        </Canvas>

        <Loader />
      </div>
      <HTMLOverlay />
    </>
  );
}
function HTMLOverlay() {
  const bcontext = useContext(ButtonContext);
  const colors = ["#0400ff", "#FF0F0F", "#12F12F", "#ffffff"];
  const changeState = () => {
    if (colors.indexOf(bcontext.color) === 3) {
      bcontext.setColor(colors[0]);

    } else {
      bcontext.setColor(colors[colors.indexOf(bcontext.color) + 1]);

    }
  };

  useEffect(() => {
    
  }, [bcontext.color]);
  return (
    <>
      <div className="fixed top-1/4 left-1/4">
        <IconButton
          
          onClick={() => {
            changeState();
          }}
          className=" drop-shadow-2xl"
        >
         <img src={img} className="w-12 h-12 " alt="wow" ></img>
        </IconButton>
      </div>
    </>
  );
}
export default function SceneWrapper(props) {
  useEffect(() => {
    if(!DataContext.initialLoad) {
      ReactGA.pageview(props.location.pathname);  
      DataContext.setInitialLoad(true)
    }
  }, [])
  const [text, setText] = useState(DefaultText.text);
  const [color, setColor] = useState(DefaultButton.color);
  return (
    <>
      <TextContext.Provider value={{ text, setText }}>
        <ButtonContext.Provider value={{ color, setColor }}>
          <ContextBridger />
          <BoxContent />
        </ButtonContext.Provider>
      </TextContext.Provider>
    </>
  );
}
