import "./App.css";
import SceneWrapper from "./components/Helpers/SceneWrapper"
import { useEffect, createContext, useState } from "react"
import ReactGA from 'react-ga';

ReactGA.initialize('G-DJ032DFCSV');

const DefaultData = {
  initialLoad: false,
  setInitialLoad: () => {}
};
export const DataContext = createContext(DefaultData);
const callback = list => {
  list.getEntries().forEach(entry => {
    console.log(entry)
      ReactGA.timing({
        category: "First Meaningful Paint",
        variable: entry.name,
        value: entry.responseEnd
      })
})
}

var observer = new PerformanceObserver(callback);
observer.observe({entryTypes: [
                                // 'navigation',
                                // 'paint',
                                // 'resource',
                                // 'mark',
                                // 'measure',
                                // 'frame',
                                // 'longtask'
                              ]})
function App() {
  useEffect(() => {
    document.title = "Muhammed's Bookshelf Portfolio"
  }, []);
  const [initialLoad, setInitialLoad] = useState(false);
  return (
    <>


      <DataContext.Provider
        value={{
          initialLoad: initialLoad,
          setInitialLoad: () => setInitialLoad(true),
        }}
      >
        <div className=" m-0 p-0  h-screen relative bg-wall bg-no-repeat bg-cover bg-center">
          <SceneWrapper />
        </div >
      </DataContext.Provider>
    </>
  );
}
export default App;
